/****************************************************************************************/
/* C9 Page Setup */
/****************************************************************************************/

html,
body {
    height: 100%;
    margin: 0;
}

body {
    color: $gray-900;
    line-height: 1.3;
    font-family: "helvetica neue",
        helvetica,
        sans-serif;
}

#page-wrapper,
.wrapper {
    padding: 0px;
    z-index: 50;
    position: relative;
}

#page-wrapper {
    min-height: 50vh;
    justify-content: center;
    align-items: center;
    display: flex;
}

@media only screen and (min-width:992px) {

    .admin-bar .fixed-top {
        top: var(--wp-admin--admin-bar--height);
    }
}

/* blog index */
.container-posts .post .entry-content p:last-child {
    margin-bottom: 5px;
}

/* blog sticky posts */
.blog .sticky,
.sticky {
    background-color: $gray-100;
}

.blog .sticky .entry-header,
.sticky .entry-header {
    padding: 1em 1em 0.5em 1em;
}

.sticky .c9-block-post-grid-header,
.sticky .c9-block-post-grid-excerpt {
    padding: 0px 1em;
}

.c9-block-post-grid .sticky .c9-block-post-grid-excerpt {
    margin-bottom: 1em;
}

.blog .sticky h3:before,
.sticky h3:before {
    font-family: FontAwesome;
    content: '\f08d';
    display: inline-block;
}

.blog .sticky figure,
.sticky figure {
    margin-bottom: 0;
}

a.skip-link {
    right: auto;
    left: 0;
}

.skip-link:focus {
    display: block;
    position: fixed !important;
    color: $light;
    border-radius: 0px;
    background-color: $dark;
    padding: 8px 20px;
    margin: 0px;
    border: 1px solid transparent;
    border-radius: 0px;
    height: auto;
    width: auto;
    overflow: visible;
    line-height: 1;
    left: 15px;
    right: auto;
    top: 65px;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-decoration: none;
    z-index: 100000;
    clip: auto;
    box-shadow: none;
    outline-color: $primary;
}

@media only screen and (min-width: 768px) {
    .skip-link:focus {
        top: 115px;
    }
}

@media only screen and (min-width: 1px) {

    .blog .entry-content,
    .single .entry-content,
    .single .entry-header,
    .single .entry-footer {
        padding: 0px 15px;
    }
}

@media only screen and (min-width: 576px) {

    .page-template-default .page .entry-content,
    .page-container .entry-header,
    .full-width-page-wrapper>.container {
        width: 100%;
        max-width: 100%;
        margin: 0px auto;
    }

    .entry-content {
        overflow: visible;
    }
}

@media only screen and (min-width: 768px) {

    .container-narrow,
    .page-container .entry-header,
    .page-template-default .page .entry-content .container {
        width: 100%;
    }

    .single .entry-image {
        margin-top: 30px;
    }

    .single .entry-footer {
        padding: 0px;
    }
}

@media only screen and (min-width: 992px) {

    .container-narrow,
    .page-template-default .page .entry-content,
    .page-container .entry-header,
    #full-width-page-wrapper>.container {
        width: 100%;
        margin: 0px auto;
    }

    .container-narrow,
    .single .entry-header {
        max-width: 960px;
    }

    .single .entry-image {
        margin: 30px auto 0px auto;
    }

    .single .entry-header {
        margin: 10px auto 0px auto;
    }

    .blog .entry-header,
    .blog .entry-content,
    .single .entry-content,
    .single .entry-header {
        padding: 0px;
    }
}

@media only screen and (min-width: 1200px) {
    .page-template-default .container-narrow {
        width: 960px;
        margin: 0px auto;
    }

    .page-template-default .page .entry-content,
    .page-container .entry-header,
    .page-template-fullwidthpage #full-width-page-wrapper>.container,
    .page-template-blank .entry-content .container {
        width: 1140px;
        margin: 0px auto;
    }
}

/****************************************************************************************/
/* Typography */
/****************************************************************************************/
:root {
    font-size: 62.5%;
}

body {
    font-size: 1.6rem;
}

.entry-content p,
.page-content p,
.c9 dl,
.entry-footer,
p {
    margin-bottom: 20px;
    line-height: 1.6;
}

.entry-content address,
.post-content>.page-links,
.entry-content>.page-links {
    font-size: 16px;
    margin-bottom: 20px;
}

.editor-styles-wrapper .wp-block-pullquote p,
.c9 .wp-block-pullquote p,
.wp-block-pullquote.is-style-solid-color blockquote p,
.wp-block-pullquote blockquote>.block-library-pullquote__content .block-editor-rich-text[data-is-empty="true"]::before,
.wp-block-pullquote blockquote>.block-editor-rich-text p,
.wp-block-pullquote blockquote p:only-of-type,
.wp-block-pullquote blockquote p:last-child {
    line-height: 1.3;
    margin-bottom: 30px;
    font-size: 1.6rem;
}

.editor-styles-wrapper .wp-block-pullquote .wp-block-pullquote__citation,
.c9 .wp-block-pullquote .wp-block-pullquote__citation,
.c9 .wp-block-pullquote cite,
.wp-block-pullquote cite,
.editor-styles-wrapper .wp-block-quote__citation,
.entry-content .wp-block-quote cite,
.wp-block-pullquote cite,
.wp-block-pullquote footer,
.wp-block-pullquote__citation,
.wp-block-pullquote.is-style-solid-color blockquote .block-editor-rich-text .wp-block-pullquote__citation {
    color: $gray-700;
    line-height: 1;
    font-style: normal;
    font-size: 1.6rem;
}

.entry-content p:last-child,
.entry-content p:only-of-type,
.wp-block-pullquote.is-style-solid-color blockquote p:last-child,
.wp-block-pullquote.is-style-solid-color blockquote p:only-of-type {
    margin-bottom: 0px;
}

pre,
kbd,
.wp-block-code,
.wp-block-preformatted {
    border: 1px solid $gray-300;
    font-size: 1.5rem;
    padding: 13px;
    background: rgba($gray-500, 0.4);
    line-height: 1.3;
    white-space: pre-wrap;
    margin-bottom: 10px;
    border-radius: 0px;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}

.c9-site-title,
.entry-content h1,
.entry-content .h1,
.wp-block-heading h1,
.entry-header h1 {
    font-size: 40px;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.entry-header h2 {
    font-size: 3rem;
}

.entry-meta {
    text-transform: uppercase;
    letter-spacing: 0.05em;
    line-height: 1.2;
    margin-bottom: 1em;
    color: $gray-800;
    font-size: 1.2rem;
}

.entry-meta a {
    color: $link-color;
}

.has-small-font-size,
.has-regular-font-size,
.has-normal-font-size,
.has-medium-font-size,
.has-large-font-size,
.has-larger-font-size,
.has-huge-font-size {
    line-height: 1.3;
}

.entry-content p.wp-block-subhead,
.subhead-h,
.c9-sh,
.editor-styles-wrapper .c9-cta .c9-sh p {
    font-weight: 300;
}

.display-1 {
    font-size: 8rem;
}

.display-2 {
    font-size: 7.3rem;
}

.display-3,
.c9-txl {
    font-size: 6.5rem;
}

.display-4 {
    font-size: 5.8rem;
}

.display-5 {
    font-size: 5rem;
}

.display-6 {
    font-size: 4.3rem;
}

/* links in content */
a,
.editor-styles-wrapper .block-editor-rich-text__editable a {
    transition: 0.3s color;
    text-decoration: underline;
    color: $link-color;
}

a:hover,
a:focus {
    color: $link-hover-color;
    text-decoration: underline;
}

a:focus {
    outline: 1px dotted $dark;
}

.wp-block-code .block-editor-plain-text {
    background-color: transparent;
}

@media only screen and (min-width: 600px) {
    .wp-block-code .block-editor-plain-text {
        font-size: 1.5rem;
    }
}

/* Responsive Typography */
@media only screen and (min-width: 1px) and (max-width: 667px) {


    .has-larger-font-size,
    .has-huge-font-size {
        line-height: 1 !important;
    }

    .single .entry-header h1 {
        line-height: 1.2;
    }
}

/****************************************************************************************/
/* WordPress Core Alignment Classes + Containers */
/****************************************************************************************/
@media only screen and (min-width: 1px) {

    .entry-content>.alignfull,
    .entry-content .container [class="wp-block-*"][data-align="full"],
    .entry-content .container .alignfull,
    .wp-block[data-align="full"],
    .page-template-containerwidthpage .entry-content .container .c9-grid>.container-fluid.alignfull {
        width: 100vw;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .page-template-default .entry-content>.wp-block-columns {
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media only screen and (min-width: 576px) {

    .entry-content>.alignfull,
    .entry-content .container [class="wp-block-*"][data-align="full"],
    .entry-content .container .alignfull,
    .wp-block[data-align="full"] {
        width: 100vw;
        max-width: 100vw;
    }

    .page-left-sidebar .entry-content>.wp-block-cover.alignfull {
        margin-left: calc((-100vw + 80.1%) / 2);
    }

    .page-right-sidebar .entry-content>.wp-block-cover.alignfull {
        margin-left: calc((-100vw + 120%) / 2);
    }
}

@media only screen and (min-width: 992px) {

    .entry-content .container .wp-block-pullquote,
    .entry-content .container .wp-block-quote,
    .entry-content .container .wp-block-embed {
        width: 100%;
        max-width: 960px;
        margin-left: auto;
        margin-right: auto;
    }

    .entry-content .container .wp-block-gallery:only-child {
        margin-bottom: 0px;
    }

    .page-template-default .entry-content .alignwide,
    .page-template-default .entry-content>p,
    .page-template-default .entry-content>h1,
    .page-template-default .entry-content>h2,
    .page-template-default .entry-content>h3,
    .page-template-default .entry-content>h4,
    .page-template-default .entry-content>h5,
    .page-template-default .entry-content>h6,
    .page-template-default .entry-content>.wp-block-columns {
        width: 100%;
        max-width: 100vw;
    }

    .page-template-default .entry-content>.alignfull,
    .entry-content .container .wp-block-embed.alignfull,
    .page-template-default .entry-content>.wp-block-columns.alignfull {
        width: 100vw;
        max-width: 100vw;
        margin-left: calc((-100vw + 100%) / 2);
        margin-right: calc((-100vw + 100%) / 2);
    }

    .editor-styles-wrapper .wp-block[data-type="core/pullquote"][data-align="right"],
    .wp-block-pullquote.alignright {
        margin-right: -50px;
    }

    .editor-styles-wrapper .wp-block[data-type="core/pullquote"][data-align="left"],
    .wp-block-pullquote.alignleft {
        margin-left: -50px;
    }
}

@media only screen and (min-width: 1200px) {

    .page-template-default .entry-content>p,
    .page-template-default .entry-content>h1,
    .page-template-default .entry-content>h2,
    .page-template-default .entry-content>h3,
    .page-template-default .entry-content>h4,
    .page-template-default .entry-content>h5,
    .page-template-default .entry-content>h6,
    .page-template-default .entry-content>.wp-block-columns,
    .page-template-default .entry-content>.wp-block-button {
        max-width: 1140px;
    }

    .page-template-default .entry-content .alignfull,
    .page-template-default .entry-content>.wp-block-columns.alignfull {
        width: 100vw;
        max-width: 100vw;
    }

    .page-template-default .entry-content hr:after {
        display: block;
        content: "";
        clear: both;
        padding-bottom: 60px;
    }

    .editor-styles-wrapper .wp-block[data-type="core/pullquote"][data-align="right"],
    .wp-block-pullquote.alignright {
        margin-right: -250px;
    }

    .editor-styles-wrapper .wp-block[data-type="core/pullquote"][data-align="left"],
    .wp-block-pullquote.alignleft {
        margin-left: -250px;
    }
}

@media only screen and (min-width: 992px) {
    .wp-block-pullquote.alignwide {
        max-width: 120%;
        margin-left: -10%;
        margin-right: -10%;
    }
}

@media only screen and (min-width: 1200px) {
    .wp-block-pullquote.alignwide {
        max-width: 140%;
        margin-left: -20%;
        margin-right: -20%;
    }
}

.alignleft,
.wp-block-gallery.alignleft {
    float: left;
    margin-right: 50px;
}

.alignright,
.wp-block-gallery.alignright {
    float: right;
    margin-left: 50px;
}

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;
}

.aligncenter:last-child,
.alignright:last-child,
.alignleft:last-child {
    margin-bottom: 0px;
}

/****************************************************************************************/
/* WordPress C9 Top Navigation */
/****************************************************************************************/
.header-navbar {
    z-index: 99;
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
}

.header-navbar .container {
    max-width: 1140px;
    padding-left: 0px;
    padding-right: 0px;
}

@media only screen and (min-width: 768px) {
    .header-navbar {
        height: 108px;
    }
}

/* set height so after nav is fixed it doesn't jump from pageheight difference*/
.header-navbar.jumpfix {
    min-height: 108px;
}

.navbar-small .header-navbar.jumpfix {
    min-height: 60px;
}

.header-navbar .navbar {
    width: 100%;
    margin: 0px auto;
    transition: opacity 0.5s ease-in;
    padding: 0 1em;
}

.header-navbar .navbar-collapse:not(.navbar-small) {
    width: 80%;
}

.header-navbar .nav .nav-item {
    text-align: left;
}

.header-navbar .nav .nav-item .nav-link,
.header-navbar .nav .nav-item .dropdown-item {
    font-family: $headings-font-family;
    font-weight: $font-weight-bold;
    font-size: 1.4rem;
    text-decoration: none;
    line-height: 1.3;
}

.header-navbar .nav .search .nav-link .fa-search {
    font-size: 1.2rem;
}

.header-navbar .nav .nav-item .dropdown-menu {
    min-width: 225px;
}

.header-navbar .nav .nav-item .dropdown-menu .dropdown-item {
    text-align: left;
}

.header-navbar .nav .nav-item .dropdown-header {
    font-family: $headings-font-family;
    color: $body-color;
    padding: 15px;
}

.dropdown-item.active,
.dropdown-item:active {
    background-color: $primary;
}

.header-navbar .nav .nav-item .nav-link {
    transition: color 0.3s ease-in;
}

.navbar-small-buttons {
    margin-left: auto;
}

.header-navbar .nav .search {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-navbar .navbar-light .navbar-toggler {
    border: 1px solid transparent;
    padding: 12px;
    margin-right: 10px;
    width: 45px;
}

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search {
    margin: 0px;
    font-size: 2rem;
    line-height: 1;
    cursor: pointer;
}

.header-navbar .navbar-brand.c9-custom-logo {
    max-width: 285px;
    max-height: 92px;
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
}

.header-navbar .navbar-small .navbar-brand.c9-custom-logo {
    max-width: 285px;
    max-height: 45px;
    width: auto;
    height: auto;
}

@media only screen and (max-width: 991px) {

    /* centers nav items in a single element per row for mobile/tablet drop down*/
    .header-navbar .nav .nav-item {
        display: block;
        text-align: center;
    }

    .header-navbar .nav .nav-item.search {
        display: none;
    }

    .header-navbar .navbar-collapse:not(.navbar-small) {
        width: 100%;
    }

    .header-navbar .navbar-collapse,
    .header-navbar .navbar-collapse.show {
        position: absolute;
        top: 100%;
        width: 100%;
        left: 0;
    }

    .header-navbar .nav .nav-item .dropdown-menu .dropdown-item {
        text-align: center;
        border: none;
    }
}

@media only screen and (min-width: 992px) {
    .navbar-brand>.c9-custom-logo>.c9-custom-logo {
        max-width: 285px;
        max-height: 92px;
        width: auto;
        height: auto;
    }

    .navbar-small>.navbar-brand>.c9-custom-logo {
        max-width: 285px;
        max-height: 45px;
        width: auto;
        height: auto;
    }

    .navbar-expand-lg>.container,
    .navbar-expand-lg>.container-fluid {
        padding-left: 0px;
        padding-right: 0px;
    }

    .header-navbar .navbar-collapse:not(.navbar-small) {
        width: 100%;
    }

    .header-navbar .nav .nav-item {
        display: inline-block;
        text-align: center;
    }

    .header-navbar .nav .nav-item .dropdown-menu {
        margin: 0px auto;
        text-align: center;
    }

    .header-navbar .nav .nav-item .dropdown-menu>.menu-item {
        text-align: left;
        display: block;
    }

    .navbar-small-buttons .nav-search {
        display: none;
    }

    /*adds search icon at end of nav links*/
    .header-navbar .nav .nav-item.search {
        display: inline-block;
    }
}

/****************************************************************************************/
/* WordPress archive, search, and woocommerce post pagination */
/****************************************************************************************/
.page-search-results nav,
.archive nav,
.blog nav {
    margin: 0px auto;
    text-align: center;
}

.page-search-results nav .pagination,
.archive .pagination,
.blog .pagination,
.entry-footer-content .page-links {
    max-width: 1140px;
    margin: 0px auto;
    text-align: center;
    display: inline-block;
}

.page-links,
.entry-footer-content .page-links {
    margin-top: 20px;
}

.page-search-results nav .pagination .page-item,
.archive .pagination .page-item,
.blog .pagination .page-item {
    display: inline-block;
}

.page-search-results nav .pagination .page-item .page-link,
.c9 .woocommerce nav.woocommerce-pagination ul li span,
.c9 .woocommerce nav.woocommerce-pagination ul li .page-numbers,
.archive nav .pagination .page-item .page-link,
.blog nav .pagination .page-item .page-link,
.single .navigation .nav-previous a,
.single .navigation .nav-next a,
.post-page-numbers {
    font-weight: 700;
    font-family: $headings-font-family;
    padding: 10px;
    color: $link-color;
}

.single .navigation .nav-previous a,
.single .navigation .nav-next a,
.navigation .dots,
.page-link.dots,
.post-page-numbers {
    text-decoration: none;
    color: $link-color;
}

.page-search-results .navigation .page-item.active .page-link,
.page-search-results .navigation .pagination li .current,
.c9 .woocommerce nav.woocommerce-pagination ul li .current.page-numbers,
.archive nav .pagination .page-item.active .current,
.blog nav .pagination .page-item.active .current {
    color: $dark;
}

.pagination .page-item:last-child .page-link:before,
.pagination .page-item:first-child .page-link:before {
    font-family: FontAwesome;
    content: '\f104';
    color: $gray-700;
}

.pagination .page-item:last-child .page-link:before {
    content: '\f105';
}

.page-link,
.page-item.active .page-link,
.post-page-numbers {
    background-color: $light;
    border: 1px solid $gray-100;
}

.page-search-results nav .page-item.active .page-link,
.page-search-results nav .pagination li .current,
.archive nav .pagination .page-item.active .current,
.blog nav .pagination .page-item.active .current,
.page-link.current {
    background-color: $gray-100;
    border: 1px solid $gray-100;
}

.pagination .page-item:first-child .page-link,
.pagination .page-item:last-child .page-link {
    border: 1px solid transparent;
}

.page-search-results nav .pagination .page-item .page-link,
.c9.woocommerce nav.woocommerce-pagination ul li .page-numbers,
.archive .pagination .page-item a,
.blog .pagination .page-item a {
    color: $link-color;
    text-decoration: none;
}

.c9.woocommerce nav.woocommerce-pagination ul li .page-numbers:hover,
.archive .pagination .page-item a:hover,
.blog .pagination .page-item a:hover,
.post-page-numbers:hover {
    color: $dark;
}

.post-page-numbers:hover {
    border: 1px solid $gray-500;
}

.c9.woocommerce nav.woocommerce-pagination,
.page-search-results nav .pagination,
.archive nav .pagination,
.blog nav .pagination {
    margin-bottom: 20px;
}

@media only screen and (max-width: 414px) {
    .entry-footer {
        padding: 0px 20px;
    }
}

/****************************************************************************************/
/* WordPress Core Widgets + Widget Blocks (archives, calendar, etc.) */
/****************************************************************************************/
.widget_archive ul,
.widget_categories ul,
.widget_pages ul,
.widget_meta ul,
.widget_recent_comments ul,
.widget_recent_entries ul,
.widget_rss ul,
.widget_text ul,
.widget_nav_menu ul,
.widget_cortex_twitter_widget ul,
.widget ul {
    list-style: none;
    margin: 15px 0;
    padding: 0;
}

.footer-entirety .textwidget {
    margin: 15px 0px;
}

.widget_archive ul li,
.widget_categories ul li,
.widget_categories ul li,
.widget_pages ul li,
.widget_meta ul li,
.widget_recent_comments ul li,
.widget_recent_entries ul li,
.widget_rss ul li,
.widget_text ul li,
.widget_nav_menu ul li,
.widget ul li {
    margin: 10px 0;
    padding-left: 0px;
}

.footer-entirety .textwidget,
.sidebar .widget .menu-item {
    font-size: 1.2rem;
}

.widget {
    margin: 0 0 5em;
}

.widget:last-child {
    margin: 0;
}

.widget h1,
.widget h2,
.widget h3 {
    font-weight: $font-weight-bold;
    letter-spacing: 0.01em;
    font-size: 1.6rem;
}

.widget select {
    max-width: 100%;
}

