    padding: 15px;
    font-size: 1rem;
}

.comment-list {
    margin-bottom: 70px;
}

.comment-list .comment-meta {
    line-height: 1.6;
}

.comment-list .comment article {
    position: relative;
    border: none;
    border-left: 1px solid $gray-100;
}

.comment-list {
    list-style: none;
    margin: 0 0 70px;
    padding: 0;
}

.comment-list .comment article:only-child,
.comment-list .comment .children {
    margin-bottom: 25px;
    margin-top: 25px;
}

.comment-list .comment .children {
    padding-left: 20px;
}

.comment-list .comment article .says {
    display: none;
}

.comment-list .comment article .avatar {
    float: left;
}

.comment-reply-link {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 4px 6px 4px 8px;
    color: $light;
    line-height: 1;
    z-index: 1;
    font-weight: $font-weight-base;
    letter-spacing: 0.2em;
    text-transform: uppercase;
}

.comment-list .comment article .reply a:after {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    height: 100%;
    width: 100%;
    opacity: 1;
    z-index: -1;
    transition: all 0.4s ease-in-out;
    background-color: $primary;
}

.comment-list .comment article .reply a:hover:after {
    opacity: 0.8;
}

#comments h3:after {
    border-bottom: 2px solid $primary;
}

.children {
    list-style: none;
}

.comment-content a {
    word-wrap: break-word;
}

.comment-content .wp-smiley {
    border: none;
    margin-bottom: 0;
    margin-top: 0;
    padding: 0;
}

.comment-awaiting-moderation {
    display: inline;
    font-size: 1rem;
}

.comment-author .fn,
.comment-metadata a,
.comment-awaiting-moderation {
    margin-left: 15px;
}

.comment-content:before,
.comment-content:after {
    content: "";
    display: table;
}

.comment-content:after {
    clear: both;
}

.comment-reply-link,
.comment-author .fn .url,
.comment-metadata a {
    transition: all 0.4s ease-in-out;
}

.comment-metadata a,
.reply a,
.entry-footer .comments-link a {
    font-weight: $font-weight-base;
    text-transform: uppercase;
    text-decoration: none;
    color: $primary;
}

.entry-footer .comments-link {
