.reply a {
    color: $light;
}

.entry-footer .comments-link a:hover,
.reply a:hover {
    color: $gray-100;
}

.comment.bypostauthor {
    border-left: 3px solid $primary;
}

.comment-reply-link:hover,
.comment-author .fn .url:hover,
.comment-metadata a:hover {
    text-decoration: none;
}

.bypostauthor {
    display: block;
}

