/******************************************** tabs **************************************/
.entry-content .nav-tabs .nav-link {
    border: none;
    border-radius: 0px;
    line-height: 1;
}

.entry-content .nav.nav-tabs {
    line-height: 1;
    font-size: 1.5rem;
    border-bottom: none;
    margin: 0px;
}

.entry-content .nav.nav-tabs .nav-item {
    margin: 0px 18px;
}

.entry-content .nav.nav-tabs .nav-item:before {
    display: none;
}

.nav-tabs .nav-link:hover,
.c9-horizontal-tabs .nav-tabs .nav-link:hover {
    color: $gray-800;
}

.c9-horizontal-tabs .nav-tabs .nav-link {
    color: $gray-600;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 0.09em;
}

.c9-horizontal-tabs .nav-tabs .nav-link.active {
    color: $gray-700;
}

.c9-horizontal-tabs .tab-content {
    padding: 25px;
}

/******************************************** grid **************************************/
//for sidebar page template grid stacking and container width stacking
@media only screen and (max-width: 667px) {
    .page-template-containerwidthpage .c9-grid>.c9-column-container>.c9-layout-column-wrap.c9-is-responsive-column {
        grid-template-columns: 1fr;
