    .page-left-sidebar .c9-grid>.c9-layout-columns-5>.c9-layout-column-wrap.c9-is-responsive-column,
    .page-right-sidebar .c9-grid>.c9-layout-columns-5>.c9-layout-column-wrap.c9-is-responsive-column {
