:root {
  font-family: "helvetica neue", helvetica, sans-serif;
  --default-font: "helvetica neue", helvetica, sans-serif;
}

/****************************************************************************************/
/* C9 Page Setup */
/****************************************************************************************/
html,
body {
  height: 100%;
  margin: 0;
}

body {
  color: #4a4a4a;
  line-height: 1.3;
  font-family: "helvetica neue", helvetica, sans-serif;
}

#page-wrapper,
.wrapper {
  padding: 0px;
  z-index: 50;
  position: relative;
}

#page-wrapper {
  min-height: 50vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media only screen and (min-width: 992px) {
  .admin-bar .fixed-top {
    top: var(--wp-admin--admin-bar--height);
  }
}
/* blog index */
.container-posts .post .entry-content p:last-child {
  margin-bottom: 5px;
}

/* blog sticky posts */
.blog .sticky,
.sticky {
  background-color: #f1f1f1;
}

.blog .sticky .entry-header,
.sticky .entry-header {
  padding: 1em 1em 0.5em 1em;
}

.sticky .c9-block-post-grid-header,
.sticky .c9-block-post-grid-excerpt {
  padding: 0px 1em;
}

.c9-block-post-grid .sticky .c9-block-post-grid-excerpt {
  margin-bottom: 1em;
}

.blog .sticky h3:before,
.sticky h3:before {
  font-family: FontAwesome;
  content: "\f08d";
  display: inline-block;
}

.blog .sticky figure,
.sticky figure {
  margin-bottom: 0;
}

a.skip-link {
  right: auto;
  left: 0;
}

.skip-link:focus {
  display: block;
  position: fixed !important;
  color: #ffffff;
  border-radius: 0px;
  background-color: #000000;
  padding: 8px 20px;
  margin: 0px;
  border: 1px solid transparent;
  border-radius: 0px;
  height: auto;
  width: auto;
  overflow: visible;
  line-height: 1;
  left: 15px;
  right: auto;
  top: 65px;
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  z-index: 100000;
  clip: auto;
  box-shadow: none;
  outline-color: #000000;
}

@media only screen and (min-width: 768px) {
  .skip-link:focus {
    top: 115px;
  }
}
@media only screen and (min-width: 1px) {
  .blog .entry-content,
.single .entry-content,
.single .entry-header,
.single .entry-footer {
    padding: 0px 15px;
  }
}
@media only screen and (min-width: 576px) {
  .page-template-default .page .entry-content,
.page-container .entry-header,
.full-width-page-wrapper > .container {
    width: 100%;
    max-width: 100%;
    margin: 0px auto;
  }

  .entry-content {
    overflow: visible;
  }
}
@media only screen and (min-width: 768px) {
  .container-narrow,
.page-container .entry-header,
.page-template-default .page .entry-content .container {
    width: 100%;
  }

  .single .entry-image {
    margin-top: 30px;
  }

  .single .entry-footer {
    padding: 0px;
  }
}
@media only screen and (min-width: 992px) {
  .container-narrow,
.page-template-default .page .entry-content,
.page-container .entry-header,
#full-width-page-wrapper > .container {
    width: 100%;
    margin: 0px auto;
  }

  .container-narrow,
.single .entry-header {
    max-width: 960px;
  }

  .single .entry-image {
    margin: 30px auto 0px auto;
  }

  .single .entry-header {
    margin: 10px auto 0px auto;
  }

  .blog .entry-header,
.blog .entry-content,
.single .entry-content,
.single .entry-header {
    padding: 0px;
  }
}
@media only screen and (min-width: 1200px) {
  .page-template-default .container-narrow {
    width: 960px;
    margin: 0px auto;
  }

  .page-template-default .page .entry-content,
.page-container .entry-header,
.page-template-fullwidthpage #full-width-page-wrapper > .container,
.page-template-blank .entry-content .container {
    width: 1140px;
    margin: 0px auto;
  }
}
/****************************************************************************************/
/* Typography */
/****************************************************************************************/
:root {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

.entry-content p,
.page-content p,
.c9 dl,
.entry-footer,
p {
  margin-bottom: 20px;
  line-height: 1.6;
}

.entry-content address,
.post-content > .page-links,
.entry-content > .page-links {
  font-size: 16px;
  margin-bottom: 20px;
}

.editor-styles-wrapper .wp-block-pullquote p,
.c9 .wp-block-pullquote p,
.wp-block-pullquote.is-style-solid-color blockquote p,
.wp-block-pullquote blockquote > .block-library-pullquote__content .block-editor-rich-text[data-is-empty=true]::before,
.wp-block-pullquote blockquote > .block-editor-rich-text p,
.wp-block-pullquote blockquote p:only-of-type,
.wp-block-pullquote blockquote p:last-child {
  line-height: 1.3;
  margin-bottom: 30px;
  font-size: 1.6rem;
}

.editor-styles-wrapper .wp-block-pullquote .wp-block-pullquote__citation,
.c9 .wp-block-pullquote .wp-block-pullquote__citation,
.c9 .wp-block-pullquote cite,
.wp-block-pullquote cite,
.editor-styles-wrapper .wp-block-quote__citation,
.entry-content .wp-block-quote cite,
.wp-block-pullquote cite,
.wp-block-pullquote footer,
.wp-block-pullquote__citation,
.wp-block-pullquote.is-style-solid-color blockquote .block-editor-rich-text .wp-block-pullquote__citation {
  color: #2b2b2b;
  line-height: 1;
  font-style: normal;
  font-size: 1.6rem;
}

.entry-content p:last-child,
.entry-content p:only-of-type,
.wp-block-pullquote.is-style-solid-color blockquote p:last-child,
.wp-block-pullquote.is-style-solid-color blockquote p:only-of-type {
  margin-bottom: 0px;
}

pre,
kbd,
.wp-block-code,
.wp-block-preformatted {
  border: 1px solid #dee2e6;
  font-size: 1.5rem;
  padding: 13px;
  background: rgba(210, 210, 210, 0.4);
  line-height: 1.3;
  white-space: pre-wrap;
  margin-bottom: 10px;
  border-radius: 0px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word;
  -ms-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}

.c9-site-title,
.entry-content h1,
.entry-content .h1,
.wp-block-heading h1,
.entry-header h1 {
  font-size: 40px;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

.entry-header h2 {
  font-size: 3rem;
}

.entry-meta {
  text-transform: uppercase;
  letter-spacing: 0.05em;
  line-height: 1.2;
  margin-bottom: 1em;
  color: #767676;
  font-size: 1.2rem;
}

.entry-meta a {
  color: #4a4a4a;
}

.has-small-font-size,
.has-regular-font-size,
.has-normal-font-size,
.has-medium-font-size,
.has-large-font-size,
.has-larger-font-size,
.has-huge-font-size {
  line-height: 1.3;
}

.entry-content p.wp-block-subhead,
.subhead-h,
.c9-sh,
.editor-styles-wrapper .c9-cta .c9-sh p {
  font-weight: 300;
}

.display-1 {
  font-size: 8rem;
}

.display-2 {
  font-size: 7.3rem;
}

.display-3,
.c9-txl {
  font-size: 6.5rem;
}

.display-4 {
  font-size: 5.8rem;
}

.display-5 {
  font-size: 5rem;
}

.display-6 {
  font-size: 4.3rem;
}

/* links in content */
a,
.editor-styles-wrapper .block-editor-rich-text__editable a {
  transition: 0.3s color;
  text-decoration: underline;
  color: #4a4a4a;
}

a:hover,
a:focus {
  color: #000000;
  text-decoration: underline;
}

a:focus {
  outline: 1px dotted #000000;
}

.wp-block-code .block-editor-plain-text {
  background-color: transparent;
}

@media only screen and (min-width: 600px) {
  .wp-block-code .block-editor-plain-text {
    font-size: 1.5rem;
  }
}
/* Responsive Typography */
@media only screen and (min-width: 1px) and (max-width: 667px) {
  .has-larger-font-size,
.has-huge-font-size {
    line-height: 1 !important;
  }

  .single .entry-header h1 {
    line-height: 1.2;
  }
}
/****************************************************************************************/
/* WordPress Core Alignment Classes + Containers */
/****************************************************************************************/
@media only screen and (min-width: 1px) {
  .entry-content > .alignfull,
.entry-content .container [class="wp-block-*"][data-align=full],
.entry-content .container .alignfull,
.wp-block[data-align=full],
.page-template-containerwidthpage .entry-content .container .c9-grid > .container-fluid.alignfull {
    width: 100vw;
    max-width: 100vw;
    margin-left: calc((-100vw + 100%) / 2);
    margin-right: calc((-100vw + 100%) / 2);
  }

  .page-template-default .entry-content > .wp-block-columns {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media only screen and (min-width: 576px) {
  .entry-content > .alignfull,
.entry-content .container [class="wp-block-*"][data-align=full],
.entry-content .container .alignfull,
.wp-block[data-align=full] {
    width: 100vw;
    max-width: 100vw;
  }

  .page-left-sidebar .entry-content > .wp-block-cover.alignfull {
    margin-left: calc((-100vw + 80.1%) / 2);
  }

  .page-right-sidebar .entry-content > .wp-block-cover.alignfull {
    margin-left: calc((-100vw + 120%) / 2);
  }
}
@media only screen and (min-width: 992px) {
  .entry-content .container .wp-block-pullquote,
.entry-content .container .wp-block-quote,
